<template>
    <div id="app">
        <div @click="$store.dispatch('modal/show')">
            <page-header />
            <div
                v-hammer:swipe.horizontal="onSwipe"
            >
                <transition
                    appear
                    @enter="enter"
                    @leave="leave"
                >
                    <router-view
                        :class="$style.page"
                    />
                </transition>
            </div>
        </div>
        <modal headline="Kunde werden und Tankkarten beantragen"></modal>
    </div>
</template>

<script>
    import { Vue, Component, Watch } from 'vue-property-decorator';
    import { Getter } from 'vuex-class';
    import views from './views';
    import filter from 'lodash/filter';
    import findIndex from 'lodash/findIndex';
    import TimelineLite from 'gsap/TimelineLite';
    import CSSPlugin from 'gsap/CSSPlugin';
    import EventBus from '@/utils/event-bus';

    const PageHeader = () => import(/* webpackChunkName: 'header' */ '@/components/Header');
    const Modal = () => import(/* webpackChunkName: "modal" */ '@/components/Modal');

    @Component({
        components: {
            PageHeader,
            Modal
        }
    })
    export default class App extends Vue {
        routes = [];
        currentIndex = null;
        switchInterval = null;

        slideInterval = 30000;

        enterTimeline = null;
        leaveTimeline = null;
        slideAnimationDuration = 0.6;

        activated = [
            CSSPlugin
        ];

        @Getter('general/getDirection') swipeDirection;
        @Getter('modal/getShow') modalOpen;

        @Watch('modalOpen')
        modalOpenChanged(val) {
            if (val) this.destroyInterval();
            else this.initInterval();
        }

        created() {
            this.routes = filter(views, view => view.name);
        }

        mounted() {
            this.initInterval();
        }

        initInterval() {
            this.destroyInterval();
            this.switchInterval = setInterval(() => {
                this.$store.commit('general/setDirection', 2);

                if (this.currentIndex === null) this.currentIndex = findIndex(this.routes, o => o.name === this.$route.name);
                
                if (this.currentIndex >= this.routes.length - 1) this.currentIndex = 0;
                else this.currentIndex++;

                EventBus.$emit('PAGE_CHANGING');
                Vue.nextTick(() => {
                    setTimeout(() => {
                        this.$router.push(this.routes[this.currentIndex].name);
                    }, 600);
                });
            }, this.slideInterval);
        }

        destroyInterval() {
            clearInterval(this.switchInterval);
            this.switchInterval = null;
        }

        destroyed() {
            this.destroyInterval();
        }

        onSwipe(e) {
            this.destroyInterval();
            this.initInterval();

            this.$store.commit('general/setDirection', e.direction);
            
            if (this.currentIndex === null) this.currentIndex = findIndex(this.routes, o => o.name === this.$route.name);
            
            if (e.direction === 2) {
                if (this.currentIndex >= this.routes.length - 1) this.currentIndex = 0;
                else this.currentIndex++;
            } else {
                if (this.currentIndex <= 0) this.currentIndex = this.routes.length - 1;
                else this.currentIndex--;
            }
            
            this.$router.push(this.routes[this.currentIndex].name);
        }
        
        enter(el, done) {
            if (this.enterTimeline !== null) {
                this.enterTimeline.kill();
                this.enterTimeline = null;
            }

            Vue.nextTick(() => {
                this.enterTimeline = new TimelineLite();

                this.enterTimeline.fromTo(el, this.slideAnimationDuration, {
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    xPercent: (this.swipeDirection === 2) ? '100%' : '-100%'
                }, {
                    xPercent: 0
                });

                this.enterTimeline.to(el, 0, {
                    clearProps: 'all',
                    onComplete: done
                });
            });
        }
        
        leave(el, done) {
            if (this.leaveTimeline !== null) {
                this.leaveTimeline.kill();
                this.leaveTimeline = null;
            }

            Vue.nextTick(() => {
                this.leaveTimeline = new TimelineLite();
                    
                this.leaveTimeline.fromTo(el, this.slideAnimationDuration, {
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    xPercent: 0
                }, {
                    xPercent: (this.swipeDirection === 2) ? '-100%' : '100%',
                    onComplete: done
                });

                this.leaveTimeline.to(el, 0, {
                    clearProps: 'all',
                    onComplete: done
                });
            });
        }
    }
</script>

