import { VuexClass, Mutation, Getter, Action } from '@/vuex-decorators';

@VuexClass
export default class ModalStore {
    moduleName = 'modal';
    show = false;
    display = false;

    @Action show({ commit }) {
        commit('setDisplay', true);
        setTimeout(() => {
            commit('setShow', true);
        }, 150);
    }

    @Action hide({ commit }) {
        commit('setShow', false);
        setTimeout(() => {
            commit('setDisplay', false);
        }, 150);
    }

    @Mutation setShow(state, show) { state.show = show; }
    @Getter getShow(state) { return state.show; }

    @Mutation setDisplay(state, display) { state.display = display; }
    @Getter getDisplay(state) { return state.display; }
}
